import { of as observableOf,  Observable } from 'rxjs';
import { Injectable } from '@angular/core';
import { Contacts, RecentUsers, UserData } from '../data/users';
import { AuthenticationService } from '../../auth/Auth.Service';

@Injectable()
export class UserService extends UserData {

  private time: Date = new Date;

  private users= {
    admin: { name:'', picture: 'assets/images/anis.png' },
    directeur: { name:'', picture: 'assets/images/nick.png' },
    technicien: { name:'', picture: 'assets/images/jack.png' },
    assistante: { name:'', picture: 'assets/images/eva.png' },
    commercial: { name: '', picture: 'assets/images/alan.png' },
   
  };
  private types = {
    mobile: 'mobile',
    home: 'home',
    work: 'work',
  };
  private contacts: Contacts[] = [
    { user: this.users.admin, type: this.types.mobile },
    { user: this.users.admin, type: this.types.home },
    { user: this.users.admin, type: this.types.mobile },
    { user: this.users.admin, type: this.types.mobile },
    { user: this.users.admin, type: this.types.home },
    { user: this.users.admin, type: this.types.work },
  ];
  private recentUsers: RecentUsers[]  = [
    { user: this.users.admin, type: this.types.home, time: this.time.setHours(21, 12)},
    { user: this.users.admin, type: this.types.home, time: this.time.setHours(17, 45)},
    { user: this.users.admin, type: this.types.mobile, time: this.time.setHours(5, 29)},
    { user: this.users.admin, type: this.types.mobile, time: this.time.setHours(11, 24)},
    { user: this.users.admin, type: this.types.mobile, time: this.time.setHours(10, 45)},
    { user: this.users.admin, type: this.types.work, time: this.time.setHours(9, 42)},
    { user: this.users.admin, type: this.types.work, time: this.time.setHours(9, 31)},
    { user: this.users.admin, type: this.types.mobile, time: this.time.setHours(8, 0)},
  ];

  getUsers(): Observable<any> {
    return observableOf(this.users);
  }

  getContacts(): Observable<Contacts[]> {
    return observableOf(this.contacts);
  }

  getRecentUsers(): Observable<RecentUsers[]> {
    return observableOf(this.recentUsers);
  }
}
